import Vue from 'vue'
import App from './App.vue'
import router from './router'
import axios from 'axios'
Vue.config.productionTip = false

import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(ElementUI);

import VueUeditorWrap from 'vue-ueditor-wrap' // ES6 Module
Vue.component('vue-ueditor-wrap', VueUeditorWrap)

import vueEditor from 'vue-editor-interline'
import 'vue-editor-interline/lib/vue-editor-interline.css'
Vue.use(vueEditor)

if(process.env.NODE_ENV=='development'){
	var serve = axios.create({	baseURL: 'https://mp.weixin.qq.com' 	})
	// var urlhost = axios.create({	  baseURL: process.env.VUE_APP_URL_HOST	})
	// var urlhost = process.env.VUE_APP_URL_HOST
	// var wxpayserve = axios.create({	  baseURL: process.env.VUE_APP_WXPAY_SERVE	})
}else{
	var serve = axios.create({	baseURL: 'https://mp.weixin.qq.com' 	})
	// var serve = axios.create({	  baseURL: 'http://api.dev.wxkingbos.com'	})
	// var urlhost = document.domain
	// var wxpayserve = axios.create({	  baseURL: process.env.VUE_APP_WXPAY_SERVE	})
}

Vue.prototype.$serve = serve;

import mixin from './API/index'
Vue.mixin(mixin);
new Vue({
  router,
  axios,
  mixin,
  render: h => h(App),
}).$mount('#app')
